// app/components/Navbar.tsx - VERSION PROPRE
'use client'

import { useAuth } from '@/contexts/AuthContext'
import Link from 'next/link'
import { useState } from 'react'
import { Menu, X } from 'lucide-react'

export default function Navbar() {
  const { user, isAuthenticated, isAdmin, isWebmaster, logout } = useAuth()
  const [isMenuOpen, setIsMenuOpen] = useState(false)

  return (
    <nav className="sticky top-0 z-50 bg-white/80 backdrop-blur-md border-b border-gray-200 shadow-sm">
      <div className="max-w-7xl mx-auto px-4">
        <div className="flex justify-between items-center h-16">
          {/* Logo */}
          <Link href="/" className="flex items-center space-x-2">
            <div className="w-10 h-10 bg-gradient-to-br from-blue-600 to-purple-600 rounded-lg flex items-center justify-center">
              <span className="text-white font-bold text-xl">EGG</span>
            </div>
            <div>
              <div className="font-bold text-gray-900">Église Grande Grâce</div>
              <div className="text-xs text-gray-500">Défalé - CDEJ-RAFISSA</div>
            </div>
          </Link>

          {/* Navigation desktop */}
          <div className="hidden md:flex items-center space-x-8">
            <Link href="/" className="text-gray-700 hover:text-blue-600 transition font-medium">
              Accueil
            </Link>
            <Link href="/about" className="text-gray-700 hover:text-blue-600 transition font-medium">
              À propos
            </Link>
            <Link href="/events" className="text-gray-700 hover:text-blue-600 transition font-medium">
              Événements
            </Link>
            
            {/* Lien admin conditionnel */}
            {(isAdmin || isWebmaster) && (
              <Link href="/admin" className="text-gray-700 hover:text-blue-600 transition font-medium">
                Administration
              </Link>
            )}

            {/* Menu utilisateur */}
            {isAuthenticated ? (
              <div className="relative group">
                <button className="flex items-center space-x-3 focus:outline-none">
                  <div className="text-right">
                    <div className="font-medium text-gray-900 text-sm">
                      {user?.first_name} {user?.last_name}
                    </div>
                    <div className="text-xs text-gray-500">
                      {isWebmaster ? 'Webmaster' : isAdmin ? 'Admin' : 'Membre'}
                    </div>
                  </div>
                  <div className="w-10 h-10 bg-gradient-to-br from-blue-100 to-purple-100 rounded-full flex items-center justify-center border-2 border-white shadow">
                    <span className="text-blue-600 font-semibold">
                      {user?.first_name?.charAt(0)}{user?.last_name?.charAt(0)}
                    </span>
                  </div>
                </button>

                {/* Dropdown menu */}
                <div className="absolute right-0 mt-2 w-48 bg-white rounded-lg shadow-lg py-2 opacity-0 invisible group-hover:opacity-100 group-hover:visible transition-all duration-200 border border-gray-200">
                  <Link
                    href="/profile"
                    className="block px-4 py-2 text-sm text-gray-700 hover:bg-gray-100"
                  >
                    Mon profil
                  </Link>
                  
                  {(isAdmin || isWebmaster) && (
                    <Link
                      href="/admin"
                      className="block px-4 py-2 text-sm text-gray-700 hover:bg-gray-100"
                    >
                      Administration
                    </Link>
                  )}
                  
                  <div className="border-t border-gray-200 my-1"></div>
                  
                  <button
                    onClick={logout}
                    className="block w-full text-left px-4 py-2 text-sm text-red-600 hover:bg-gray-100"
                  >
                    Se déconnecter
                  </button>
                </div>
              </div>
            ) : (
              <div className="flex items-center space-x-4">
                <Link
                  href="/login"
                  className="text-gray-700 hover:text-blue-600 transition font-medium"
                >
                  Connexion
                </Link>
                <Link
                  href="/register"
                  className="px-4 py-2 bg-gradient-to-r from-blue-600 to-purple-600 text-white rounded-lg hover:from-blue-700 hover:to-purple-700 transition shadow"
                >
                  Inscription
                </Link>
              </div>
            )}
          </div>

          {/* Bouton menu mobile */}
          <button
            onClick={() => setIsMenuOpen(!isMenuOpen)}
            className="md:hidden p-2 text-gray-700 hover:text-blue-600"
          >
            {isMenuOpen ? <X size={24} /> : <Menu size={24} />}
          </button>
        </div>

        {/* Menu mobile */}
        {isMenuOpen && (
          <div className="md:hidden py-4 border-t border-gray-200">
            <div className="space-y-4">
              <Link href="/" className="block text-gray-700 hover:text-blue-600 transition font-medium">
                Accueil
              </Link>
              <Link href="/about" className="block text-gray-700 hover:text-blue-600 transition font-medium">
                À propos
              </Link>
              <Link href="/events" className="block text-gray-700 hover:text-blue-600 transition font-medium">
                Événements
              </Link>
              
              {(isAdmin || isWebmaster) && (
                <Link href="/admin" className="block text-gray-700 hover:text-blue-600 transition font-medium">
                  Administration
                </Link>
              )}

              {isAuthenticated ? (
                <>
                  <Link href="/profile" className="block text-gray-700 hover:text-blue-600 transition font-medium">
                    Mon profil
                  </Link>
                  <button
                    onClick={logout}
                    className="block w-full text-left text-red-600 hover:text-red-700 transition font-medium"
                  >
                    Se déconnecter
                  </button>
                </>
              ) : (
                <>
                  <Link href="/login" className="block text-gray-700 hover:text-blue-600 transition font-medium">
                    Connexion
                  </Link>
                  <Link
                    href="/register"
                    className="block px-4 py-2 bg-gradient-to-r from-blue-600 to-purple-600 text-white rounded-lg hover:from-blue-700 hover:to-purple-700 transition text-center"
                  >
                    Inscription
                  </Link>
                </>
              )}
            </div>
          </div>
        )}
      </div>
    </nav>
  )
}